<template>
  <div class="home">
    <!-- 头部 -->
    <van-sticky :offset-top="0">
      <div class="header">
        看点资讯精选
      </div>
      <van-tabs color="#2E8FF4">
        <van-tab v-for="item in categories" :key='item.id'>
           <div class="tab-title" slot="title" @click="findChildHandler(item.id)">{{ item.name }}</div>
            <van-tabs color="#2E8FF4">
              <van-tab v-for="item in childs" :key='item.id'>
                  <div class="tab-title" slot="title" @click="findArticleHandler(item.id)">{{ item.name }}</div>
              </van-tab>
            </van-tabs>
        </van-tab>
      </van-tabs>
    </van-sticky>

    <div class="wenzhangyangshi">
    <!-- {{articles}} -->
      <div class="article"
      v-for='item in articles' 
      :key="item.id"
      @click="ShowArticlesHandler(item.id)">
        <van-row style="font-size:14px; margin-left:8px; " span="24">{{item.title}} </van-row>
        <div style="margin-left:5px;margin-right:5px;height:60px;" justify="space-around">
          <span style="margin-left:5px;margin-right:5px;"><img :src="item.video" alt=",,,"/></span>
          <span style="margin-left:5px;margin-right:5px;"><img :src="item.music" alt=",,,"/></span>
          <span style="margin-left:5px;margin-right:5px;"><img :src="item.source" alt=",,,"/></span>
        </div>
        <div style="font-size:12px; margin-left:8px;">{{item.author.nickname}}{{item.publishtime}}</div>
      </div>
    </div>
    

    <!-- {{categories}} -->
    <!-- {{articles}} -->
    <!-- {{childs}} -->

   
  </div>
</template>
<script>
import {mapState, mapActions} from 'vuex'
export default {
  data() {
      return{
      
      }
  },
  computed:{
    ...mapState('category',['categories']),
    ...mapState('article',['articles','childs']),
  },
  created(){
      this.findAllCategories();
  },
  mounted(){
    setTimeout(()=>{
      this.findAllCategories();
      this.findChildHandler(this.categories[0].id)
    },500)
  },
  methods:{
    ...mapActions('category',['findAllCategories']),
    ...mapActions('article',['QueryArticles','findChild']),
    // 页面跳转文章详情页面
    ShowArticlesHandler(id){
      console.log("文章id",id)
      this.$router.push({path:'/xianshi/article',query:{id}})
    },
    
    //查询子栏目
    findChildHandler(id){
      this.findChild(id).then((res)=>{
        this.QueryArticles(this.childs[0].id)
      })
    },
    //分类查询文章
    findArticleHandler(id){
      console.log("子栏目id",id)
      this.QueryArticles(id)
    }
  }
}
</script>
<style scoped>
img {
   height:60px;
   width:30%;
   margin-bottom:0;
}
.wenzhangyangshi {
  margin-bottom: 3em;
}
