<template>
  <div class="register">
      <van-nav-bar
        title="注册"
        left-text="返回"
        left-arrow
        @click-left="backLogin"/>
        <!-- 手机号、验证码 -->
        <div class="register_t">
            <van-field
                left-icon="phone-o"
                v-model="telephone"
                label="手机号"
                placeholder="请输入手机号 (即用户名)"/>
            <van-field
                left-icon="comment-circle-o"
                v-model="vcode"
                center
                clearable
                label="短信验证码"
                placeholder="请输入短信验证码"
                >
                <van-button 
                slot="button" 
                size="small" 
                type="primary"
                @click="duanHandler()">发送验证码</van-button>
            </van-field>
            <van-field
                left-icon="chat-o"
                v-model="password"
                center
                clearable
                label="密码"
                placeholder="请输入密码">
            </van-field>
        </div>
        <!-- /手机号、验证码 -->
        <div style="text-align:center;padding:1em" >
            <van-button 
                type="info"
                round 
                @click="backHandler" 
                style="width:100%">注册
            </van-button>
        </div>
    </div>
</template>
<script>
import {mapState,mapActions} from 'vuex'
import Vue from 'vue';
import { Notify } from 'vant';
Vue.use(Notify);
export default {
    data() {
    return {
        vcode:'',
        password:'',
        telephone:''
    };
  },
  methods:{
    ...mapActions('register',['GetVerCode','CustomerRegister']),
    //短信验证
    duanHandler(){
        let form = {
           phoneNum:this.telephone,
           text:"524376",
        };
        // 成功通知
        Notify({ type: 'success', message: '发送成功' });
        this.GetVerCode(form)
    },
    // 返回登录页面
    backHandler(){
        if(this.vcode=="524376"){
            this.params = {
            username:this.telephone,
            password:this.password,
            }
        this.CustomerRegister(this.params)
        this.$router.push({path:'/login'})
        }
        
    },
    // 返回按钮
    backLogin() {
        this.$router.push({path:'/login'})
    }
  }
}
</script>
<style scoped>
    .register_t{
        margin-top:.5em;
    }
</style>