<template>
    <div class="useredit">
         <van-nav-bar
        title="修改信息"
        left-text="返回"
        left-arrow
        @click-left="backListHandler1"
        />
        <!-- 顶部导航栏 -->
        <!-- {{info}} -->
        <div class="info" :data="info">
            <van-cell-group>
            <van-field
                v-model="info.username"
                clearable
                label="用户名"
                icon="question-o"
                placeholder="请输入用户名"
            />
            <van-field
                v-model="info.password"
                type="password"
                label="密码"
                name="pattern"
                placeholder="请输入至少6位密码"
                />
             <van-field
                v-model="info.nickname"
                clearable
                label="昵称"
                icon="question-o"
                />
                 <van-field
                v-model="info.email"
                clearable
                label="邮箱"
                icon="question-o"
                />
            </van-cell-group>           
            <van-button 
                @click="backListHandler" 
                size="large" 
                type="info">确定修改
            </van-button>
        </div>
        <!-- /顶部导航栏 -->
    </div>
</template>
<script>
import {mapState,mapActions} from 'vuex'
import { Toast } from 'vant';
export default {
    data() {
        return {
            // password: '123123',
        }
    },
    computed: {
        ...mapState("user",["info"])
    },
    created() {
        
    },
    methods: {
        ...mapActions('useredit',['UpdateUser']),
        // 回到个人页面
        backListHandler1(){
            this.$router.push({path:'/manager/users'})
        },
        backListHandler(){
            if(this.info.username && this.info.password ){
                let form = {
                    id:this.info.id,
                    username:this.info.username,
                    password:this.info.password,
                    nickname:this.info.nickname,
                    email:this.info.email,
                    userface:this.info.userface,
                    role:this.info.role,
                    enabled:this.info.enabled,
                }
                this.UpdateUser(form)
                this.$router.push({path:'/manager/users'})
            } else {
                this.$notify({ type: 'danger', message: '请输入用户名或密码，谢谢！' });
            }
            
        }
       
    }
}
</script>

<style>
.van-col .one{
    border-bottom: 1px solid #efefef;
    padding:1em
}
</style>