<template>
  <div class="home">
     <van-nav-bar
        title="文章详情"
        left-text="返回"
        left-arrow
        @click-left="backListHandler1"
        />
    <!-- 文章详情 -->
    <div style="text-align:left; margin:1em 1.5em;" >
      <h4 style="text-align:center;">{{details.title}}</h4>
      <div style="text-align:center; font-size:10px;" v-if="details.author">{{details.author.username}}{{details.publishtime}}</div>
      <p style="text-align:left; font-size:13px;">{{details.content}}</p>
      <p style="text-align:right; font-size:17px; margin-top:1em"  v-if="token" >
        <van-icon name="good-job" color="#2E8FF4" @click="upHandler"/>  {{details.thumbup}}
        <van-icon name="star" color="#2E8FF4" @click="downHandler"/>  {{details.thumbdown}}
      </p>
    </div>
    <van-divider>评论区</van-divider>
    <div style="font-size:15px; margin:1em 1.5em;" v-if="details.comments">评论 {{details.comments.length}}
      <span v-if="token == null">(如需评论点赞，请先登录)</span> 
    </div>
    <div style="text-align:left; margin-top:1.5em" v-if="details.comments">
      <div v-for="item in details.comments" 
      style="margin:1em 1.5em; text-align:left;" 
      :key="item.id">
        <div style="font-size:12px;color:blue;height:20px" v-if="details.comments.customer">{{item.customer.nickname}}</div>
        <div style="font-size:12px;height:20px">{{item.content}}</div>
        <div style="font-size:10px;height:15px">{{item.commenttime}}</div>
      </div>
    </div>
    
    <!-- /文章详情 -->
    <!--发表评论 -->
      <!-- {{message}} -->
      <!-- {{comments}} -->
      <!-- {{details}} -->
      <van-sticky :offset-top="400" style="margin-bottom:3em" v-if="token" >
        <van-field
        v-model="message"
        rows="1"
        autosize
        type="textarea"
        style="border-top:1px solid #efefef"
        placeholder="请输入评论内容">
        <van-button 
        slot="button" 
        size="small" 
        type="info"
        @click="commentHandler">发表评论</van-button>
        </van-field>
      </van-sticky>
    <!-- 发表评论 -->
  </div>
</template>
<script>
import {mapState, mapActions} from 'vuex'
import {get,post} from '../../http/axios'
import Vue from 'vue';
import { Notify } from 'vant';
Vue.use(Notify);
export default {
  data() {
      return{
        message:'',
      }
  },
  computed:{
    ...mapState("user",["token","info"]),
    ...mapState("article",["details"]),
    ...mapState("comments",["comments"]),
  },
  created(){
    console.log("传递id",this.$route.query.id);
    this.readArticle(this.$route.query.id);
    this.findComments(this.$route.query.id);
  },
  methods:{
    ...mapActions('article',['readArticle','SaveOrUpdateArticle']),
    ...mapActions('comments',['UpdateComments','findComments']),
    // 页面跳转主页
    backListHandler1(){
      this.$router.push({path:'/xianshi/home'})
    },
    //发表评论
    commentHandler(){
      // console.log("当前时间",new Date().toLocaleString())
      // if(this.token){
        let form = {
          commenttime:new Date().toLocaleString(),
          articleId:this.$route.query.id,
          customerId:this.info.id,
          content:this.message,    
        }
        if (form.content !== '') {
          console.log("form值",form)
          this.UpdateComments(form)
          this.message = null;
          this.readArticle(this.$route.query.id)
        } else {
          Notify({ type: 'warning', message: '请输入评论内容' });
        }
        
      // }else{
      //    this.$router.push({path:'/login'})
      // }
    },
    //点赞
    upHandler(){
      let form = {
          id:this.$route.query.id,
          title:this.details.title,
          liststyle:this.details.liststyle,
          publishtime:this.details.publishtime,
          readtimes:this.details.readtimes,
          status:this.details.status,
          thumbup:this.details.thumbup+1,
          thumbdown:this.details.thumbdown,
          music:this.details.music,
          video:this.details.video,
          userId:this.details.author.id,
          categoryId:this.details.category.id,
          content:this.details.content,
          source:this.details.source,
      }
      // console.log("文章表单",form)
       this.SaveOrUpdateArticle(form).then((res)=>{
        this.readArticle(this.$route.query.id)
      }) 
      Notify({ type: 'primary', message: '点赞成功' });
    },
    //收藏
    downHandler(){
      let form = {
          id:this.$route.query.id,
          title:this.details.title,
          liststyle:this.details.liststyle,
          publishtime:this.details.publishtime,
          readtimes:this.details.readtimes,
          status:this.details.status,
          thumbup:this.details.thumbup,
          thumbdown:this.details.thumbdown+1,
          music:this.details.music,
          video:this.details.video,
          userId:this.details.author.id,
          categoryId:this.details.category.id,
          content:this.details.content,
          source:this.details.source,
      }
      console.log("文章表单",form)
      this.SaveOrUpdateArticle(form).then((res)=>{
        this.readArticle(this.$route.query.id)
      }) 
      Notify({ type: 'primary', message: '收藏成功' });
    },
  }
}
</script>
<style scoped>
.header {
  height: 50px;
  line-height: 50px;
  background-color: #2E8FF4;
  font-size: 17px;
  color: white;
  font-weight: bold;
  text-align: center;
}
</style>