import {get, post} from '../../http/axios'
export default {
    namespaced:true,
    state: {
        users:[]
    },
    mutations: {
        refreshUsers(state,users){
            state.users = users;
        }
    },
    actions: {
        // 查询用户信息
        async findAllUser({commit}){
            // console.log('form',form)
            let response = await get('/manager/user/findAllUser')
            commit('refreshUsers',response.data)
         },
        // 修改用户信息
        async UpdateUser({dispatch},form){
            // console.log('form',form)
            let response = await post('/manager/user/reg',form)
            dispatch('findAllUser')
            return response
       },
       
    }
} 