import {post} from '../../http/axios'
export default {
    namespaced:true,
    state: {
        vcode:'',
        cusregister:{}
    },
    mutations: {
        refreshVcode(state,vcode){
            state.vcode = vcode
        },
        refreshCusRegister(state,cusregister){
            state.cusregister = cusregister
        }
    },
    actions: {
        // 顾客进行注册
        async CustomerRegister({commit},params){
            let response = await post('/manager/user/reg',params)
            commit('refreshCusRegister',response.data)
        },
        // 获取验证码
        async GetVerCode({commit},form){
            let response = await post('/manager/user/getSmsCode',form)
            commit('refreshVcode',response.data)
        }
    }
}