import {post,get} from '../../http/axios'
export default {
    namespaced:true,
    state: {
        comments:[]
    },
    mutations :{
        refreshComments(state,comments){
            state.comments = comments

        }
    },
    actions :{
        // 根据文章id查询评论
        async findComments({commit},id){
            let response = await get('/manager/comment/findCommentByArticleId?id='+id)
            commit('refreshComments',response.data)
        },
        // 添加修改评论
        async UpdateComments({dispatch},form){
            let response = await post('/manager/comment/saveOrUpdateComment',form)
            dispatch('UpdateComments')
            return response
        },
    }
}