import Vue from 'vue'
import VueRouter from 'vue-router'

//管理页面
import Manager from '../views/manager/Index'
import Users from '../views/manager/Users'
import UsersEdit from '../views/manager/UsersEdit'

//访问页面
import Home from '../views/xianshi/Home'
import Xianshi from '../views/xianshi/Index'
import User from '../views/xianshi/User'
import Article from '../views/xianshi/Article'

//登录注册页面
import Register from '../views/Register'
import Login from '../views/Login'

import { getToken } from '../utils/auth'
import { Toast } from 'vant'
import store from '../store'

Vue.use(VueRouter)

const routes = [
  {
    path:"/",
    redirect:"/xianshi/home"
  },
  {
    path: '/manager',
    name: 'manager',
    component: Manager,
    beforeEnter: (to, from, next) => {  //属于路由自己的拦截器
      let token = getToken();
      if(token){
        // 查询info
        store.dispatch('user/info',token)
        .then(()=>{
          // 当获取万用户信息之后才允许跳转
          next();
        })
      } else {
        Toast("token失效")
        // 跳转到登录
        next({path:'/login'})
      }
    },
    children:[{
        path: 'users',
        component: Users,
      },{
        path: 'usersedit',
        component: UsersEdit,
      }]
  },
  {
    path: '/xianshi',
    name: 'xianshi',
    component: Xianshi,
    children:[{
        path: 'home',
        component:Home,
      },{
        path: 'user',
        component: User,
      },{
        path: 'article',
        component: Article,
      }]
  },
  {
    path: '/login',
    name: 'login',
    component: Login
  },
  {
    path: '/register',
    name: 'register',
    component: Register,
  }
]

const router = new VueRouter({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
