<template>
  <!-- 后台管理总页面 -->
  <div class="manager">
    <!-- 用于接受管理页面，首页，我的页面 -->
    <router-view></router-view>
    <van-tabbar route active-color="#2E8FF4">
      <van-tabbar-item icon="home-o" to="/xianshi/home">首页</van-tabbar-item>
      <van-tabbar-item icon="friends-o" @click="userHandler()">我的</van-tabbar-item>
    </van-tabbar>
  </div>
</template>
<script>
import {mapState,mapActions} from 'vuex'
export default {
  created(){
    
  },
  computed:{
    ...mapState("user",["token"])
  },
  methods:{
    userHandler(){
      if(this.token){
        this.$router.push({path:'/manager/users'})
      }else{
        this.$router.push({path:'/xianshi/user'})
      }
    }
  }
}
</script>