<template>
  <div class="login">
    <div class="header">
      <div class="logo">News</div>
      <div class="title">
        看点资讯精选
        <span style="font-size:.6em">手机端</span>
      </div>
    </div>
    <div class="form">
      <van-cell-group>
        <van-field
          v-model="form.username"
          required
          clearable
          label="用户名"
          icon="question-o"
          placeholder="请输入用户名"
        />
        <van-field
          v-model="form.password"
          type="password"
          label="密码"
          placeholder="请输入密码"
          required
        />
      </van-cell-group>
      <div style="text-align:center;padding:1em" >
       <van-button 
        plain round 
        @click="loginHandler" 
        style="width:100%">登录</van-button>
      </div>
       <!-- 注册 -->
        <div class="register">
          <button @click="RegisterHandler">
            尚未注册，点击注册
          </button>
        </div>
        <!-- /注册 -->
    </div>
  </div>
</template>
<script>
import {mapActions} from 'vuex'
export default {
  data(){
    return {
      form:{
        type:'customer'
      }
    }
  },
  methods:{
    ...mapActions("user",["login"]),
     // 注册
    RegisterHandler(){
      // alert("注册")
      this.$router.push({path:'/register'})
    },
    // 登录
    loginHandler(){
      this.login(this.form)
      .then(()=>{
        //跳转到首页
        this.$router.push({path:'/manager/users'})
      })
    }
  }
}
</script>
<style scoped>
.register{
  font-size:12px;
  width: 100%;
  color: #2E8FF4;
}
.register button{
  border:none;
  margin-left: 110px;
  background-color: #ffffff;
  cursor: pointer;
}
.header {
  height: 140px;
  padding: 50px;
  color: #ffffff;
  background-color: #2E8FF4;
  text-align: center
}
.header .logo {
  width: 50px;
  height: 50px;
  line-height: 50px;
  border-radius: 50%;
  border: 1px solid #efefef;
  margin: 0 auto;
  color:#efefef;
  font-weight: bold;
  margin-bottom: 1em;
  font-size: 1em
}
.header .title {
  font-size: 24px;
}
</style>